import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Rugu from './views/rugu/rugu.vue'
import Zhanbi from './views/rugu/zhanbi.vue'
import user from './views/rugu/user.vue'
import Jizhang from './views/caiwu/jizhang.vue'
import Kucun from './views/caiwu/kucun.vue'
let routes = [{
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'ivu-icon ivu-icon-bag', //class
        children: [
            { path: '/rugu', component: Rugu, name: '' },
            { path: '/zhanbi', component: Zhanbi, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'ivu-icon ivu-icon-android-home',
        children: [
            { path: '/jizhang', component: Jizhang, name: '' },
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


